import StrictlyGoods from '@/components/common/StrictlyGoods.vue'
export default {
	data() {
		return {
			query: '', // 获取传递参数
			adPositione: '', // 获取广告位信息
			orderId: '',
			shopId: ''
		};
	},
	components: {
		StrictlyGoods
	},
	onLoad(options) {
		this.orderId = options.orderId;
		this.shopId = options.shopId;
	},
	methods: {
		// 广告位url跳转
		goPath(url) {
			if (url) {
				location.href = url
			}
		},
		// 查看订单详情
		checkOrderDetal() {
			// 详情需要 orderId, shopId
			uni.navigateTo({
				url: '/pages/order/goodsDetail/goodsDetail?orderId=' + this.orderId + '&shopId=' + this.shopId
			})
		},
		// 返回首页
		goHome() {
			uni.switchTab({
				url: '/pages/main/main'
			})
		}
	}
}
